.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .16s;
  transition: .16s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  -webkit-transition: .16s;
  transition: .16s;
  border-radius: 50%;
}

#ico-button {
  color: #d13b24;
  font-size: 24px;
  cursor: pointer;
  position: absolute;
  right: 36px;
  transition: color .16s;
}

#ico-button:hover {
  color: #f2230c;
}