.autoExpand {
    width: calc(100% - 48px);
    resize: none;
    display: block;
    box-sizing: padding-box;
    overflow: hidden;
    padding: 10px;
    padding-top: 11px;
    font-size: 18px;
    border: none;
    border-left: 1px solid #808080;
    background: transparent;
    margin-top: -18px;
    margin-bottom: 12px;
    margin-left: 8px;
    font-family: 'Spartan';
    transition: border-left 0.3s;
}

.autoExpand:hover {
    border-left: 1px solid;
}

.autoExpand:focus {
    border-left: 1px solid;
}